import React, { useState, useEffect } from "react";
import { useMutation, useQueryClient, useQuery } from "@tanstack/react-query";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Textarea } from "@/components/ui/textarea";
import { Loader2, Phone, Shield, Settings, AlertCircle, ChevronDown, ChevronRight, Bot } from "lucide-react";
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible";
import { toast } from "sonner";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { createPhoneNumber } from "@/api/services/phoneNumbers/phoneNumberService";
import { fetchAllAgentsComplete } from "@/api/services/agents/agentService";
import { useAuth } from "@/context/AuthContext";
import { usePermissions } from "@/context/PermissionContext";

interface CreatePhoneNumberFormProps {
  onSuccess?: () => void;
  onCancel?: () => void;
}

interface FormData {
  provider: string;
  phoneNumber: string;
  name: string;
  assistantId: string;
  squadId: string;
  workflowId: string;
  fallbackDestination: string;
  serverUrl: string;
  serverUrlSecret: string;
  // Provider-specific credentials
  twilioAccountSid: string;
  twilioAuthToken: string;
  vonageApiKey: string;
  vonageApiSecret: string;
  telnyxApiKey: string;
  telnyxApiSecret: string;
  // VAPI specific
  areaCode: string;
}

const CreatePhoneNumberForm: React.FC<CreatePhoneNumberFormProps> = ({
  onSuccess,
  onCancel
}) => {
  const { user } = useAuth();
  const { isSuperAdmin } = usePermissions();

  const [formData, setFormData] = useState<FormData>({
    provider: "vapi", // Default to VAPI
    phoneNumber: "",
    name: "",
    assistantId: "",
    squadId: "",
    workflowId: "",
    fallbackDestination: "",
    serverUrl: "",
    serverUrlSecret: "",
    twilioAccountSid: "",
    twilioAuthToken: "",
    vonageApiKey: "",
    vonageApiSecret: "",
    telnyxApiKey: "",
    telnyxApiSecret: "",
    areaCode: ""
  });

  const [isAdvancedOpen, setIsAdvancedOpen] = useState(false);

  const queryClient = useQueryClient();

  // Fetch assistants based on user role
  const { data: assistantsData, isLoading: assistantsLoading } = useQuery({
    queryKey: ["assistants", "complete", user?.id],
    queryFn: () => fetchAllAgentsComplete(""),
    retry: 1,
    enabled: !!user?.id,
  });

  const assistants = assistantsData?.data || [];

  const createMutation = useMutation({
    mutationFn: createPhoneNumber,
    onSuccess: () => {
      toast.success("Phone number created successfully!");
      queryClient.invalidateQueries({ queryKey: ["phoneNumbers"] });
      onSuccess?.();
    },
    onError: (error: any) => {
      toast.error(error.response?.data?.message || "Failed to create phone number");
    }
  });

  const handleInputChange = (field: keyof FormData, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const formatPhoneNumber = (phone: string) => {
    // Simple phone number formatting
    const cleaned = phone.replace(/\D/g, '');
    if (cleaned.length === 10) {
      return `+1${cleaned}`;
    } else if (cleaned.length === 11 && cleaned.startsWith('1')) {
      return `+${cleaned}`;
    }
    return phone;
  };

  const handlePhoneNumberChange = (value: string) => {
    const formatted = formatPhoneNumber(value);
    handleInputChange('phoneNumber', formatted);
  };

  const validateForm = () => {
    if (!formData.provider) {
      toast.error("Please select a provider");
      return false;
    }
    if (!formData.phoneNumber) {
      toast.error("Please enter a phone number");
      return false;
    }

    // Validate phone number format (skip for VAPI as it uses area code)
    if (formData.provider !== 'vapi') {
      const phoneRegex = /^\+[1-9]\d{1,14}$/;
      if (!phoneRegex.test(formData.phoneNumber)) {
        toast.error("Phone number must be in E.164 format (e.g., +1234567890)");
        return false;
      }
    } else {
      // Validate area code for VAPI
      if (!formData.areaCode || formData.areaCode.length !== 3) {
        toast.error("Please enter a valid 3-digit area code");
        return false;
      }
    }

    // Validate server URL if provided
    if (formData.serverUrl) {
      try {
        new URL(formData.serverUrl);
      } catch {
        toast.error("Server URL must be a valid URL");
        return false;
      }
    }

    // Validate fallback destination JSON if provided
    if (formData.fallbackDestination) {
      try {
        JSON.parse(formData.fallbackDestination);
      } catch {
        toast.error("Fallback destination must be valid JSON");
        return false;
      }
    }

    // Provider-specific validation
    switch (formData.provider) {
      case 'vapi':
        // No additional validation needed for VAPI
        break;
      case 'twilio':
        if (!formData.twilioAccountSid || !formData.twilioAuthToken) {
          toast.error("Twilio Account SID and Auth Token are required");
          return false;
        }
        if (!formData.twilioAccountSid.startsWith('AC')) {
          toast.error("Twilio Account SID should start with 'AC'");
          return false;
        }
        if (formData.twilioAccountSid.length !== 34) {
          toast.error("Twilio Account SID should be 34 characters long");
          return false;
        }
        break;
      case 'vonage':
        if (!formData.vonageApiKey || !formData.vonageApiSecret) {
          toast.error("Vonage API Key and Secret are required");
          return false;
        }
        break;
      case 'telnyx':
        if (!formData.telnyxApiKey) {
          toast.error("Telnyx API Key is required");
          return false;
        }
        break;
    }

    return true;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (!validateForm()) {
      return;
    }

    // Prepare form data for submission
    const submitData = {
      ...formData,
      assistantId: formData.assistantId === "unassigned" ? "" : formData.assistantId
    };

    createMutation.mutate(submitData);
  };

  const renderProviderCredentials = () => {
    switch (formData.provider) {
      case 'vapi':
        return (
          <div className="space-y-4">
            <div className="flex items-center gap-2 text-sm font-medium" style={{ color: '#12a594' }}>
              <Shield className="h-4 w-4" />
              AI CRUITMENT Free Phone Number
            </div>
            <div className="rounded-lg p-4 border" style={{ backgroundColor: '#fafefd', borderColor: '#12a594' }}>
              <div className="flex items-start gap-3">
                <div className="flex-shrink-0">
                  <svg className="h-5 w-5" style={{ color: '#12a594' }} viewBox="0 0 20 20" fill="currentColor">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                  </svg>
                </div>
                <div>
                  <h4 className="text-sm font-medium" style={{ color: '#008071' }}>No credentials required!</h4>
                  <p className="text-sm mt-1" style={{ color: '#008071' }}>
                    AI CRUITMENT provides free US phone numbers instantly. Just enter your desired area code and we'll assign you a random number in that area.
                  </p>
                  <ul className="text-xs mt-2 space-y-1" style={{ color: '#12a594' }}>
                    <li>• Up to 10 free numbers per account</li>
                    <li>• Instant activation (2-3 minutes)</li>
                    <li>• US numbers only</li>
                    <li>• No monthly fees</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        );

      case 'twilio':
        return (
          <div className="space-y-4">
            <div className="flex items-center gap-2 text-sm font-medium text-blue-600">
              <Shield className="h-4 w-4" />
              Twilio Credentials
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <Label htmlFor="twilioAccountSid">Account SID *</Label>
                <Input
                  id="twilioAccountSid"
                  type="text"
                  placeholder="ACxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx"
                  value={formData.twilioAccountSid}
                  onChange={(e) => handleInputChange('twilioAccountSid', e.target.value)}
                  className="font-mono text-sm"
                />
              </div>
              <div>
                <Label htmlFor="twilioAuthToken">Auth Token *</Label>
                <Input
                  id="twilioAuthToken"
                  type="password"
                  placeholder="Your Twilio Auth Token"
                  value={formData.twilioAuthToken}
                  onChange={(e) => handleInputChange('twilioAuthToken', e.target.value)}
                  className="font-mono text-sm"
                />
              </div>
            </div>
          </div>
        );

      case 'vonage':
        return (
          <div className="space-y-4">
            <div className="flex items-center gap-2 text-sm font-medium text-orange-600">
              <Shield className="h-4 w-4" />
              Vonage Credentials
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <Label htmlFor="vonageApiKey">API Key *</Label>
                <Input
                  id="vonageApiKey"
                  type="text"
                  placeholder="Your Vonage API Key"
                  value={formData.vonageApiKey}
                  onChange={(e) => handleInputChange('vonageApiKey', e.target.value)}
                  className="font-mono text-sm"
                />
              </div>
              <div>
                <Label htmlFor="vonageApiSecret">API Secret *</Label>
                <Input
                  id="vonageApiSecret"
                  type="password"
                  placeholder="Your Vonage API Secret"
                  value={formData.vonageApiSecret}
                  onChange={(e) => handleInputChange('vonageApiSecret', e.target.value)}
                  className="font-mono text-sm"
                />
              </div>
            </div>
          </div>
        );

      case 'telnyx':
        return (
          <div className="space-y-4">
            <div className="flex items-center gap-2 text-sm font-medium text-purple-600">
              <Shield className="h-4 w-4" />
              Telnyx Credentials
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <Label htmlFor="telnyxApiKey">API Key *</Label>
                <Input
                  id="telnyxApiKey"
                  type="password"
                  placeholder="Your Telnyx API Key"
                  value={formData.telnyxApiKey}
                  onChange={(e) => handleInputChange('telnyxApiKey', e.target.value)}
                  className="font-mono text-sm"
                />
              </div>
              <div>
                <Label htmlFor="telnyxApiSecret">API Secret (Optional)</Label>
                <Input
                  id="telnyxApiSecret"
                  type="password"
                  placeholder="Your Telnyx API Secret (if required)"
                  value={formData.telnyxApiSecret}
                  onChange={(e) => handleInputChange('telnyxApiSecret', e.target.value)}
                  className="font-mono text-sm"
                />
              </div>
            </div>
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Phone className="h-5 w-5 text-blue-600" />
            Create Phone Number
          </CardTitle>
          <CardDescription>
            Get a free AI CRUITMENT phone number.
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Provider Selection */}
          <div className="space-y-2">
            <Label htmlFor="provider">Provider *</Label>
            <Select value={formData.provider} onValueChange={(value) => handleInputChange('provider', value)}>
              <SelectTrigger>
                <SelectValue placeholder="Select a provider" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="vapi" className="font-medium">
                  <div className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full" style={{ backgroundColor: '#12a594' }}></div>
                    AI CRUITMENT (Free US Number)
                  </div>
                </SelectItem>
              </SelectContent>
            </Select>
          </div>

          {/* Basic Information */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {formData.provider === 'vapi' ? (
              <div>
                <Label htmlFor="areaCode" className="flex items-center gap-2">
                  Area Code *
                  <span className="text-xs px-2 py-0.5 rounded-full text-white font-medium" style={{ backgroundColor: '#12a594' }}>
                    Free
                  </span>
                </Label>
                <div className="relative">
                  <Input
                    id="areaCode"
                    type="text"
                    placeholder="000"
                    value={formData.areaCode}
                    onChange={(e) => {
                      const value = e.target.value.replace(/\D/g, '').slice(0, 3);
                      handleInputChange('areaCode', value);
                      handleInputChange('phoneNumber', value); // Use area code as phone number for backend
                    }}
                    className="font-mono text-lg text-center border-2 focus:border-[#12a594] focus:ring-[#12a594]"
                    style={{ borderColor: formData.areaCode ? '#12a594' : undefined }}
                    maxLength={3}
                  />
                  {formData.areaCode && (
                    <div className="absolute right-3 top-1/2 transform -translate-y-1/2">
                      <svg className="h-4 w-4" style={{ color: '#12a594' }} fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                      </svg>
                    </div>
                  )}
                </div>
                <p className="text-xs mt-1" style={{ color: '#12a594' }}>
                  Enter a 3-digit US area code (e.g., 415, 212, 310)
                </p>
              </div>
            ) : (
              <div>
                <Label htmlFor="phoneNumber">Phone Number *</Label>
                <Input
                  id="phoneNumber"
                  type="tel"
                  placeholder="+1 (555) 123-4567"
                  value={formData.phoneNumber}
                  onChange={(e) => handlePhoneNumberChange(e.target.value)}
                  className="font-mono"
                />
                <p className="text-xs text-gray-500 mt-1">
                  Enter the phone number in E.164 format
                </p>
              </div>
            )}
            <div>
              <Label htmlFor="name">Friendly Name</Label>
              <Input
                id="name"
                type="text"
                placeholder={formData.provider === 'vapi' ? "My AI CRUITMENT Number" : "My Business Number"}
                value={formData.name}
                onChange={(e) => handleInputChange('name', e.target.value)}
              />
            </div>
          </div>

          {/* Assistant Selection */}
          <div className="space-y-2">
            <Label htmlFor="assistantId" className="flex items-center gap-2">
              <Bot className="h-4 w-4" />
              Assistant
            </Label>
            <Select value={formData.assistantId} onValueChange={(value) => handleInputChange('assistantId', value)}>
              <SelectTrigger>
                <SelectValue placeholder="Select an assistant (optional)" />
              </SelectTrigger>
              <SelectContent>
                {assistantsLoading ? (
                  <div className="flex items-center justify-center p-2">
                    <Loader2 className="h-4 w-4 animate-spin" />
                    <span className="ml-2">Loading assistants...</span>
                  </div>
                ) : assistants.length === 0 ? (
                  <div className="flex items-center justify-center p-2 text-gray-500">
                    No assistants available
                  </div>
                ) : (
                  <>
                    <SelectItem value="unassigned">
                      <span className="text-gray-500">No assistant assigned</span>
                    </SelectItem>
                    {assistants.map((assistant) => (
                      <SelectItem key={assistant.id} value={assistant.assistant_id || assistant.id}>
                        <div className="flex items-center gap-2">
                          <Bot className="h-3 w-3" />
                          {assistant.name}
                        </div>
                      </SelectItem>
                    ))}
                  </>
                )}
              </SelectContent>
            </Select>
            <p className="text-xs text-gray-500">
              {isSuperAdmin
                ? "Super admin can see all assistants"
                : "You can only see your own assistants"
              }
            </p>
          </div>

          {/* Provider Credentials */}
          {formData.provider && (
            <div className="border rounded-lg p-4 bg-gray-50">
              {renderProviderCredentials()}
            </div>
          )}

          {/* Advanced Configuration */}
          {/* <Collapsible open={isAdvancedOpen} onOpenChange={setIsAdvancedOpen}>
            <CollapsibleTrigger asChild>
              <Button
                variant="ghost"
                className="flex items-center gap-2 text-sm font-medium text-gray-700 hover:text-gray-900 p-0 h-auto"
              >
                {isAdvancedOpen ? (
                  <ChevronDown className="h-4 w-4" />
                ) : (
                  <ChevronRight className="h-4 w-4" />
                )}
                <Settings className="h-4 w-4" />
                Advanced Configuration (Optional)
              </Button>
            </CollapsibleTrigger>
            <CollapsibleContent className="space-y-4 mt-4">

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <Label htmlFor="squadId">Squad ID</Label>
                <Input
                  id="squadId"
                  type="text"
                  placeholder="Squad ID to assign"
                  value={formData.squadId}
                  onChange={(e) => handleInputChange('squadId', e.target.value)}
                  className="font-mono text-sm"
                />
              </div>
              <div>
                <Label htmlFor="workflowId">Workflow ID</Label>
                <Input
                  id="workflowId"
                  type="text"
                  placeholder="Workflow ID to assign"
                  value={formData.workflowId}
                  onChange={(e) => handleInputChange('workflowId', e.target.value)}
                  className="font-mono text-sm"
                />
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <Label htmlFor="serverUrl">Server URL</Label>
                <Input
                  id="serverUrl"
                  type="url"
                  placeholder="https://your-server.com/webhook"
                  value={formData.serverUrl}
                  onChange={(e) => handleInputChange('serverUrl', e.target.value)}
                />
              </div>
              <div>
                <Label htmlFor="serverUrlSecret">Server URL Secret</Label>
                <Input
                  id="serverUrlSecret"
                  type="password"
                  placeholder="Webhook secret"
                  value={formData.serverUrlSecret}
                  onChange={(e) => handleInputChange('serverUrlSecret', e.target.value)}
                />
              </div>
            </div>

            <div>
              <Label htmlFor="fallbackDestination">Fallback Destination</Label>
              <Textarea
                id="fallbackDestination"
                placeholder="Fallback destination configuration (JSON)"
                value={formData.fallbackDestination}
                onChange={(e) => handleInputChange('fallbackDestination', e.target.value)}
                rows={3}
                className="font-mono text-sm"
              />
              <p className="text-xs text-gray-500 mt-1">
                Optional fallback destination for call routing
              </p>
            </div>
            </CollapsibleContent>
          </Collapsible> */}

          {/* Security Notice */}
          {formData.provider && formData.provider !== 'vapi' && (
            <Alert>
              <AlertCircle className="h-4 w-4" />
              <AlertDescription>
                Your credentials are securely stored and encrypted. They are only used to authenticate with {formData.provider} for phone number management.
              </AlertDescription>
            </Alert>
          )}
        </CardContent>
      </Card>

      {/* Action Buttons */}
      <div className="flex justify-end gap-3">
        <Button
          type="button"
          variant="outline"
          onClick={onCancel}
          disabled={createMutation.isPending}
        >
          Cancel
        </Button>
        <Button
          type="submit"
          disabled={createMutation.isPending}
          className="min-w-[120px]"
          style={formData.provider === 'vapi' ? {
            backgroundColor: '#12a594',
            borderColor: '#12a594',
            color: 'white'
          } : undefined}
        >
          {createMutation.isPending ? (
            <>
              <Loader2 className="h-4 w-4 mr-2 animate-spin" />
              {formData.provider === 'vapi' ? 'Getting Free Number...' : 'Creating...'}
            </>
          ) : (
            <>
              <Phone className="h-4 w-4 mr-2" />
              {formData.provider === 'vapi' ? 'Get Free Number' : 'Create Phone Number'}
            </>
          )}
        </Button>
      </div>
    </form>
  );
};

export default CreatePhoneNumberForm;
